import print from "@/utils/print.js"
import faIcon from "@/components/kilvn-fa-icon/fa-icon.vue"
import tkiQrcode from '@/components/tki-qrcode/tki-qrcode.vue'
import tkiBarcode from "@/components/tki-barcode/tki-barcode.vue"
import uniFab from '@/components/uni-fab/uni-fab.vue'

let that;
let uid;
export default {
	components: {
		faIcon,
		tkiQrcode,
		tkiBarcode,
		uniFab
	},
	data() {
		return {
			select_qrcode: '',
			get_reserve_checked: true, //分库存显示控制
			product: {},
			is_show: false, //二维码显示
			bar_code_show: false, //条形码显示
		}
	},
	onLoad(options) {
		that = this;
		let stocks = [];
		uid = uni.getStorageSync("uid");

		console.log(options)

		if (options.id) {
			const query = Bmob.Query('Goods');
			if (options.type == "false") {
				query.equalTo("objectId", "==", options.id);
			} else {
				query.equalTo("productCode", "==", options.id)
			}
			query.equalTo("userId", "==", uid);
			query.find().then(res => {
				console.log(res)
				let product = res[0];
				let all_reserve = 0;

				query.equalTo("userId", "==", uid);
				query.equalTo("status", "!=", -1);
				query.include("stocks");
				query.equalTo("goodsName", "==", product.goodsName);
				query.find().then(res => {

					for (let item of res) {
						let stocks_o = {}
						stocks_o.stock_name = item.stocks.stock_name
						stocks_o.stock_objectid = item.stocks.objectId
						stocks_o.reserve = item.reserve
						stocks_o.warning_num = item.warning_num
						stocks_o.bad_num = item.bad_num
						stocks_o.good_id = item.objectId
						stocks_o.accessory = (item.accessory) ? item.accessory : ''
						stocks_o.productCode = (item.productCode) ? item.productCode : item.objectId
						item.stocks = stocks_o
						all_reserve += item.reserve
						stocks.push(item.stocks)
					}

					this.product = product;
					this.product.all_reserve = all_reserve;
					this.product.stocks = stocks
					console.log(this.product)
				})
			})
		} else {
			let product = uni.getStorageSync("now_product");
			let all_reserve = 0;

			const query = Bmob.Query('Goods');
			query.equalTo("userId", "==", uid);
			query.equalTo("status", "!=", -1);
			query.include("stocks");
			query.equalTo("goodsName", "==", product.goodsName);
			query.find().then(res => {

				for (let item of res) {
					let stocks_o = {}
					stocks_o.stock_name = item.stocks.stock_name
					stocks_o.stock_objectid = item.stocks.objectId
					stocks_o.reserve = item.reserve
					stocks_o.warning_num = item.warning_num
					stocks_o.bad_num = item.bad_num
					stocks_o.good_id = item.objectId
					stocks_o.accessory = (item.accessory) ? item.accessory : ''
					stocks_o.productCode = (item.productCode) ? item.productCode : item.objectId
					item.stocks = stocks_o
					all_reserve += item.reserve
					stocks.push(item.stocks)
				}

				this.product = uni.getStorageSync("now_product");
				this.product.all_reserve = all_reserve;
				this.product.stocks = stocks
				console.log(this.product)
			})
		}


	},

	methods: {
		//生成二维码
		show_qrcode(item) {
			that.is_show = true,
				that.select_qrcode = item.productCode
		},

		//分库存的switch点击
		change_state(e) {
			that.get_reserve_checked = e.detail.value
		},

		//商品信息点击
		print_info(item) {
			print.print_goodDet(item)
		},

		//产品信息修改点击
		modify(item) {
			console.log(item)
			let now_product = uni.getStorageSync("now_product")
			now_product.objectId = item.good_id
			now_product.reserve = item.reserve
			now_product.warning_num = (item.warning_num) ? item.warning_num : 0
			now_product.bad_num = (item.bad_num) ? item.bad_num : 0
			
			uni.showActionSheet({
				itemList: ['编辑产品信息', '编辑产品的库存信息'],
				success: function(res) {
					console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
					if (res.tapIndex == 0) {
						uni.navigateTo({
							url: 'edit_info/edit_info'
						});
						now_product.ids = that.product.stocks
						console.log(that.product.stocks)
						uni.setStorageSync("now_product", now_product)
					} else {
						if (item.stock_name) {
							let warehouse = []
							let stock = {}
							let _stock = {}
							_stock.stock_name = item.stock_name
							_stock.objectId = item.stock_objectid
							stock.stock = _stock
							warehouse.push(stock)
							uni.setStorageSync("warehouse", warehouse)
						}
						uni.navigateTo({
							url: 'edit_stock/edit_stock'
						});
						
						uni.setStorageSync("now_product", now_product)
					}
				},
			});

		},

		//fab列目点击
		trigger(e) {
			this.content[e.index].active = !e.item.active;
			if (e.index == 0) {
				uni.setStorageSync("now_product", this.product)
				uni.navigateTo({
					url: '../good_add/good_add'
				});
			} else {
				that.delete()
			}
		},

		//点击显示二维码的操作
		showcode_option() {
			uni.showActionSheet({
				itemList: ['二维码', '条形码'],
				success: function(res) {
					console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
					if (res.tapIndex == 0) {
						that.is_show = true
					} else {
						that.bar_code_show = true
					}
				},
				fail: function(res) {
					console.log(res.errMsg);
				}
			});
		},
		//二维码路径
		qrR(res) {
			this.src = res
		},

		bcR(res) {
			this.src = res
		},

		saveBccode() {
			this.$refs.barcode._saveCode()
		},

		//点击条形码保存
		saveQrcode() {
			this.$refs.qrcode._saveCode()
		},

		//删除商品
		delete_good(objectId, accessory, index) {
			uni.showModal({
				title: '提示',
				content: '是否删除该商品',
				success: function(res) {
					if (res.confirm) {

						const query = Bmob.Query('Goods');
						query.set('id', objectId) //需要修改的objectId
						query.set('status', -1)
						query.save().then(res => {

							if (accessory) {
								uni.navigateTo({
									url: '../goods/goods'
								});

								setTimeout(function() {
									uni.showToast({
										title: "删除成功"
									})
								}, 1000)
							} else {
								if (that.product.stocks.length > 1) {
									let next_good = that.product.stocks[index + 1].good_id
									const query = Bmob.Query('Goods');
									query.get(next_good).then(res => {
										console.log(res)
										res.set('accessory', false)
										res.save()
										uni.navigateTo({
											url: '../goods/goods'
										});

										setTimeout(function() {
											uni.showToast({
												title: "删除成功"
											})
										}, 1000)
									}).catch(err => {
										console.log(err)
									})
								} else {
									uni.navigateTo({
										url: '../goods/goods'
									});

									setTimeout(function() {
										uni.showToast({
											title: "删除成功"
										})
									}, 1000)
								}

							}


						}).catch(err => {
							console.log(err)
						})

					}
				}
			});

		},

	},
}
